.container{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 7000;
    background: rgba(255,255,255,0.95);
    width: 100%;
    height: 100vh;
}

.errorBox{
    width: auto;
    height: auto;
    padding: 1rem;
    /* box-shadow: 1px 1px 2px rgb(94, 177, 255); */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border :1px solid rgb(94, 177, 255);
}
.errorBox h2{
    text-align: left;
    font-size: 1.6rem;
    margin: 1rem 0;
}
.errorBox p{
    font-size: 1.4rem;
    line-height: 2rem;
}
.button{
    border: 1px solid #ddd;
    background: rgb(130, 184, 255);
    padding: 0.5rem 2rem;
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #111;
    border-radius: 0.4rem;
    cursor: pointer;
}