.container{
   width: 100%;
   min-height: 100%;
   margin-top: 12vh;
}
.header{
    width: 100%;
    height: 12vh;
    border-bottom: 1px solid rgb(248, 245, 245);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    position: fixed;
    top:0;
    left: 0;
    z-index: 6000;
}
.menuBox{
    width: 4rem;
    height: 4rem;
    display: none;
}

.menuBox svg{
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.menuBox svg use{
    width: 100%;
    height: 100%;
    fill: rgb(77, 164, 245);
}

.logo{
    width: 30%;
    height: 10rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: #3483eb;
}
.logo h2,h4{
    font-weight: 500;
}
.logo h4{
    font-style: italic;
}
.logo  img {
    width: 5rem;
    height: auto;
}
.nav{
    width: 70%;
    height: auto;
    margin: auto auto;   
}

.nav ul{
    width: auto;
    height: 4rem;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
}
.nav ul li{
    width: auto;
    height: 4rem;
    margin: auto auto;
    padding: 1rem;
}
.nav ul > li > a{
    width: 100%;
    padding: 0 1rem;
    min-height: 5rem;
    text-decoration: none;
    color:#112;
    font-weight: 300;
}
.nav ul > li:hover a{
    color: #3489eb;
}

.footer{
    width: 100%;
    height: 14vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 5rem;
    font-size: 1.4rem;
    background-size: contain;
}

.logoutButton{
    border: 0px;
    box-shadow: 1px 1px 1px  #8ab5e5;
    padding: 1rem 2rem;
    margin-right: 1rem;
    border-radius: 0.4rem;
    background:  #3489eb;
    color: #fff;
    margin-left: 30%;
}
.doctorName{
    position: absolute;
    bottom: 0rem;
    left: 0;
    text-align: center;
    width: 100%;
    font-size: 1.2rem;
    
}
@media screen and (max-width : 800px){
    .nav{
        width: 65%;
    }
    .logo{
        width: 25%;
    }
    .logo h2{
        font-size: 1.6rem;
    }
    .logo h3{
        font-size: 1.4rem;
    }
    .footer{
        padding:0rem 0.5rem;
        flex-wrap: wrap;
    }
    .footer_left{
        width: 100%;

    }
    .footer_right{
        width: 100%;
        
    }
    .logoutButton {
        margin-left: 0;
    }
}



@media screen and (max-width : 700px){
 .header{
     padding: 0 2%;
 }
 .nav{
     display: none;
 }
 .logo{
     width: 25rem;
     justify-content: flex-start;
 }
 .logo img{
     width: 4rem;
     margin-right: 3rem;
 }
 .logo h2{
     font-size: 1.8rem;
 }
 .logo h4{
     font-size: 1.6rem;
 }
 .menuBox{
     display: block;
 }
 .show{
     display: block;
 }
 nav.show{
    position: fixed;
    top: 12vh;
    left: 0;
     width: 100%;
     height: 100%;
     background: #fff;
     z-index: 8000;
 }
 .show ul{
     flex-direction: column;
     justify-content: flex-start;
     width: 100%;
     height: 100%;
     padding: 1rem 10%;
 }
 .show ul li{
     width: 100%;
     height: 4rem;
     margin: 0.5rem 0;
     border-bottom: 1px solid rgb(245, 241, 241);
 }
 .show ul li a{
     margin: 0 ;
     width: 100%;
     height: 4rem;
 }
 .logoutButton {
    margin-left: 0;
    position: fixed;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%,-50%);
}
}